import Endpoint from '@/services/Endpoint'

export default {
  branches (payload) {
    return Endpoint().get('/s1/bank/branch', {
      params: payload
    })
  },
  createBranch (payload) {
    return Endpoint().post('/s1/bank/branch', payload)
  },
  modifyBranch (payload) {
    return Endpoint().patch('/s1/bank/branch', payload)
  },
  contactMechs (payload) {
    return Endpoint().get(`/s1/bank/branch/${payload.branchId}/contactMech`, {
      params: payload
    })
  },
  createContactMech (payload) {
    return Endpoint().post(`/s1/bank/branch/${payload.branchId}/contactMech`, payload)
  },
  deleteContactMech (payload) {
    return Endpoint().delete(`/s1/bank/branch/${payload.branchId}/contactMech`, {
      params: payload
    })
  },
  workShifts (payload) {
    return Endpoint().get(`/s1/bank/branch/${payload.branchId}/workShift`, {
      params: payload
    })
  },
  createWorkShift (payload) {
    return Endpoint().post(`/s1/bank/branch/${payload.branchId}/workShift`, payload)
  },
  deleteWorkShift (payload) {
    return Endpoint().delete(`/s1/bank/branch/${payload.branchId}/workShift`, {
      params: payload
    })
  },
  branchFunctions (payload) {
    return Endpoint().get(`/s1/bank/branch/${payload.branchId}/bankFunction`, {
      params: payload
    })
  }
}
