import BranchService from '@/services/bank/BranchService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let BranchTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultBranchTableFilter: {
      type: String,
      default: null
    },
    defaultBranchTableOrderBy: {
      type: String,
      default: null
    },
    bankId: {
      type: String,
      default: null
    },
    acceptsPayments: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      branchTableData: [],
      branchTableFilterTimer: null,
      branchTableFilter: this.defaultBranchTableFilter,
      branchTableOrderBy: this.defaultBranchTableOrderBy,
      branchTableContainer: 'loading-container',
      branchTableBusy: false,
      branchTableTotalRows: 0,
      branchTableCurrentPage: 1,
      branchTablePerPage: 10
    }
  },
  computed: {
    branchTableNoDataText: function () {
      return this.branchTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    branchTableTotalPages: function () {
      return Math.ceil(this.branchTableTotalRows / this.branchTablePerPage)
    },
    branchTablePagingInfo: function () {
      return this.pagingInfo(this.branchTableCurrentPage, this.branchTablePerPage, this.branchTableTotalRows)
    }
  },
  methods: {
    loadBranchTableData (loadingContainerId) {
      let self = this
      this.branchTableBusy = true
      this.branchTableContainer = loadingContainerId || this.branchTableContainer
      this.startLoading(this.branchTableContainer)
      BranchService.branches({
        pageIndex: this.branchTableCurrentPage - 1,
        pageSize: this.branchTablePerPage,
        filter: this.branchTableFilter,
        orderByField: this.branchTableOrderBy,
        bankId: this.bankId,
        acceptsPayments: this.acceptsPayments,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.branchTableContainer)
        self.branchTableBusy = false
        self.branchTableTotalRows = response.data.totalRows
        self.branchTableData = response.data.branchList || []
      }).catch(function (error) {
        self.stopLoading(self.branchTableContainer)
        self.branchTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshBranchTable () {
      this.loadBranchTableData()
    },
    filterBranchTable (filter) {
      let self = this
      if (this.branchTableFilter !== filter) {
        clearTimeout(this.branchTableFilterTimer)
        this.branchTableFilterTimer = setTimeout(() => {
          self.branchTableFilter = filter
          self.loadBranchTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.branchTableFilterTimer)
      }
    },
    orderBranchTable (orderBy, active) {
      this.branchTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBranchTableData()
    }
  },
  watch: {
    branchTableCurrentPage: function () {
      this.refreshBranchTable()
    },
    branchTablePerPage: function () {
      this.refreshBranchTable()
    },
    bankId: function () {
      this.refreshBranchTable()
    }
  }
}

export default BranchTableMixin
